.table{
    margin-top: 20px;
    overflow-y: scroll;
    height: 400px;
    color: rgb(61, 61, 61);
}

.table tr{
    display: flex;
    justify-content: space-between;
}
.table td{
    padding: 0.5rem;
}
.table tr:nth-of-type(odd){
    background-color: #e5e5e5;
    border-radius: 2px;
}