@import url(https://fonts.googleapis.com/css2?family=Volkhov:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
}
.app{
  display: flex;
  justify-content: space-evenly;
  padding: 20px;
  background-color: rgb(73, 72, 72);

}
body{
  background-color: #e5e5e5;
}
.app_header{
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;


  border-radius: 10px;
}

h1{
  font-family: 'Volkhov', serif;
    font-size: 46px;
    font-weight: bolder;
    color: #f53123;
    margin-bottom: 20px;
    margin-top: 20px;
    letter-spacing: 0.1rem;
}
.sidebarhead{
  font-family: 'Volkhov', serif;
    font-size: 20px;
    font-weight: 900;
    color: #f3ebeb;
    margin-bottom: 20px;
    margin-top: 20px;
}
.last{
  color: white;
}
.MuiInputBase-input {
  font: inherit;
  width: 100%;
  border: 0;
  height: 1.1876em;
  color: #e5e5e5;
  margin: 0;
  display: block;
  padding: 6px 0 7px;
  min-width: 0;
  background: none;
  box-sizing: content-box;
  animation-name: mui-auto-fill-cancel;
  letter-spacing: inherit;
  animation-duration: 10ms;
  -webkit-tap-highlight-color: transparent;}

.sidebarhead2{
  font-family: 'Volkhov', serif;
    font-size: 20px;
    font-weight: 900;
    color: #252525;
    margin-bottom: 20px;
    margin-top: 20px;
    padding-left: 10px;
}

.app_left{
  flex:0.9 1; 
}
.app_stats{
  display: flex;
  justify-content: space-between;
}
.app_right{
  border-radius: 20px;
}
.right{
  background-color: #e43c30;
  padding-bottom: 30px;
  margin-bottom: 20px;
  border-radius: 10px;
}

@media (max-width: 990px){
  .app{
    flex-direction: column;
  }
}
.infobox{
    flex: 1 1;
    padding: 10px;
}
.infobox:not(:last-child){
    margin-right: 15px;
}
.infobox_cases {
    color: #cc1034;
    font-weight: 600;
    font-size: 1.75rem;
    margin-bottom: 0.5rem;
  }
  
  .infobox_total {
    color: #6c757d;
    font-weight: 700 !important;
    font-size: 0.8rem !important;
    margin-top: 15px !important;
  }
.table{
    margin-top: 20px;
    overflow-y: scroll;
    height: 400px;
    color: rgb(61, 61, 61);
}

.table tr{
    display: flex;
    justify-content: space-between;
}
.table td{
    padding: 0.5rem;
}
.table tr:nth-of-type(odd){
    background-color: #e5e5e5;
    border-radius: 2px;
}
.map {
    height: 500px;
    background-color: white;
    padding: 1rem;
    border-radius: 20px;
    margin-top: 16px;
    box-shadow: 0 0 8px -4px rgba(0, 0, 0, 0.5);
  }
  
  .map .leaflet-container {
    height: 100%;
    border-radius: 12px;
  }

  .info-name {
    font-size: 20px;
    font-weight: bold;
    color: #555;
  }
  
  .info-container {
    width: 150px;
  }
  
  .info-flag {
    height: 80px;
    width: 100%;
    background-size: cover;
    border-radius: 8px;
  }
  
  .info-confirmed,
  .info-recovered,
  .info-deaths {
    font-size: 16px;
    margin-top: 5px;
  }
    
  
